<template lang="pug">
div
	.factorian-content-block.factorian-breadcroumb.section-gray
		.container
				.row
					.col-md-6
						h2 Noticias

				hr
				template(v-for="noticias in Global.noticias.all" v-if="Global.noticias.all.length > 0")
					.row
						.col-sm-3(v-for="noticia in noticias")
							.single-service-item.wow.fadeInUp
								.foto(:style="{background:'url(' + Global.url +'uploads/post/' + noticia.imagen + ')'}")
								h3 {{ noticia.titulo }}
								p {{ noticia.bajada }}
								a.service-readmore-btn(@click="verNoticia(noticia)") 
									span ver mas 
									i.fa.fa-long-arrow-right
					hr
</template>

<script>
import { mapState } from 'vuex'
export default {
   
   computed: {
      ... mapState(['Global']) 
   },
   created () {
      this.$store.dispatch('Global/NOTICIAS')
   },
	methods: {
		verNoticia(noticia){
			this.$store.commit('Global/SET_NOTICIA', noticia)
			this.$router.push({ name: 'verNoticia', params: { id: noticia.id }})
		}
	}
}
</script>


